.courseContainer {
  position: relative;
}

.courseContainer .courseCard {
  position: relative;
  width: 25vw;
  height: 100vh;
  border-radius: 20px;
  overflow: hidden;
}

.courseContainer .courseCard:hover:before {
  -webkit-clip-path: circle(300px at 80% -20%);
          clip-path: circle(300px at 80% -20%);
}

.courseContainer .courseCard:after {
  position: absolute;
  top: 30%;
  left: -20%;
  font-size: 12em;
  font-weight: 800;
  font-style: italic;
  color: rgba(255, 255, 255, 0.04);
}

.courseContainer .courseCard .imgBx {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 9;
  width: 100%;
  height: 100%;
  transition: 0.5s;
}

.courseContainer .courseCard:hover .imgBx {
  top: 0%;
  -webkit-transform: translateY(-25%);
          transform: translateY(-25%);
  /* bug  */
}

.courseContainer .courseCard .imgBx img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(20deg);
          transform: translate(-50%, -50%) rotate(20deg);
  width: 270px;
}

.courseContainer .courseCard .courseContent {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  transition: 1s;
  z-index: 10;
}

.courseContainer .courseCard:hover .courseContent {
  height: 210px;
}

.courseContainer .courseCard .courseContent h2 {
  position: relative;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
}

.courseContainer .courseCard .courseContent .courseDesc,
.courseContainer .courseCard .courseContent .color {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: left;
          justify-content: left;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 20px;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.courseContainer .courseCard:hover .courseContent .courseDesc {
  opacity: 1;
  visibility: visible;
  transition-delay: 0.5s;
}

.courseContainer .courseCard:hover .courseContent .color {
  opacity: 1;
  visibility: visible;
  transition-delay: 0.6s;
}

.courseContainer .courseCard .courseContent .courseDesc h3,
.courseContainer .courseCard .courseContent .color h3 {
  color: white;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-right: 10px;
}

.courseContainer .courseCard .courseContent .courseDesc span {
  width: 26px;
  height: 26px;
  text-align: center;
  line-height: 26px;
  font-size: 20px;
  display: inline-block;
  color: #111;
  background: #fff;
  margin: 0 5px;
  transition: 0.5s;
  color: #111;
  border-radius: 4px;
  cursor: pointer;
}

.courseContainer .courseCard .courseContent .courseDesc span:hover {
  /* other bug */
  background: #b90000;
}

.courseContainer .courseCard .courseContent .color span {
  width: 20px;
  height: 20px;
  background: #ff0;
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
}

.courseContainer .courseCard .courseContent .color span:nth-child(2) {
  background: #1bbfe9;
}

.courseContainer .courseCard .courseContent .color span:nth-child(3) {
  background: #1b2fe9;
}

.courseContainer .courseCard .courseContent .color span:nth-child(4) {
  background: #080481;
}

.courseContainer .courseCard .courseContent a {
  display: inline-block;
  border-radius: 4px;
  padding: 20px 21px;
  background: border-box;
  color: #fabe00;
  text-decoration: none;
  font-weight: 600;
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  transition: 0.5s;
  text-align: center;
  width: 100%;
}

.courseContainer .courseCard:hover .courseContent a {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition-delay: 0.7s;
}
